
"use client";

import { useState } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { CheckCircle, Shield, KeyRound, FaceIcon, Fingerprint, Loader2, Phone, UserCheck, AlertCircle, AlertTriangle } from "lucide-react";
import { cn } from "@/lib/utils";
import { useToast } from "@/hooks/use-toast";
import { Alert, AlertTitle, AlertDescription } from "@/components/ui/alert";

type VerificationStep = "otp" | "face" | "fingerprint" | "complete";

const stepConfig: Record<VerificationStep, { title: string; icon: React.ElementType; description: string; }> = {
  otp: {
    title: "Step 1: Mobile OTP",
    icon: KeyRound,
    description: "A one-time password will be sent to your registered mobile number.",
  },
  face: {
    title: "Step 2: Face Recognition",
    icon: FaceIcon,
    description: "Position your face within the frame for a liveness check.",
  },
  fingerprint: {
    title: "Step 3: Fingerprint Scan",
    icon: Fingerprint,
    description: "Use your device's biometric sensor to confirm your identity.",
  },
  complete: {
    title: "Verification Complete",
    icon: CheckCircle,
    description: "Your identity has been successfully verified.",
  },
};

export function IdentityVerificationClient() {
  const { toast } = useToast();
  const [step, setStep] = useState<VerificationStep>("otp");
  const [isProcessing, setIsProcessing] = useState(false);
  
  // OTP State
  const [phone, setPhone] = useState("+966501234567");
  const [otpSent, setOtpSent] = useState(false);
  const [otp, setOtp] = useState("");

  const handleSendOtp = () => {
    setIsProcessing(true);
    setTimeout(() => {
      setOtpSent(true);
      toast({
        title: "OTP Sent",
        description: `A 4-digit code (1234) was sent to ${phone}.`,
      });
      setIsProcessing(false);
    }, 1000);
  };

  const handleVerifyOtp = () => {
    setIsProcessing(true);
    setTimeout(() => {
      if (otp === "1234") {
        setStep("face");
        toast({
          title: "OTP Verified",
          className: 'bg-green-100 border-green-300'
        });
      } else {
        toast({
          variant: "destructive",
          title: "Invalid OTP",
          description: "Please try again.",
        });
      }
      setIsProcessing(false);
    }, 1000);
  };
  
  const handleFaceScan = () => {
    setIsProcessing(true);
    setTimeout(() => {
        setStep("fingerprint");
         toast({
          title: "Face Scan Successful",
          className: 'bg-green-100 border-green-300'
        });
        setIsProcessing(false);
    }, 2500); // Longer delay for simulation
  };

  const handleFingerprintScan = () => {
    setIsProcessing(true);
    setTimeout(() => {
        setStep("complete");
        toast({
          title: "Identity Verified!",
          description: "All security checks passed.",
          className: 'bg-green-100 border-green-300'
        });
        setIsProcessing(false);
    }, 1500);
  };

  const handleReset = () => {
    setStep("otp");
    setOtp("");
    setOtpSent(false);
  }

  const renderStepContent = () => {
    switch (step) {
      case "otp":
        return (
          <div className="space-y-4">
             <div className="space-y-2">
                <Label htmlFor="phone">Mobile Number</Label>
                <div className="flex items-center gap-2">
                    <Phone className="h-5 w-5 text-muted-foreground" />
                    <Input id="phone" value={phone} disabled />
                </div>
            </div>
            {!otpSent ? (
                <Button onClick={handleSendOtp} disabled={isProcessing} className="w-full">
                    {isProcessing ? <Loader2 className="mr-2 h-4 w-4 animate-spin"/> : <KeyRound className="mr-2 h-4 w-4" />}
                    Send OTP
                </Button>
            ) : (
                <div className="space-y-4 pt-2">
                    <div className="space-y-2">
                        <Label htmlFor="otp">Enter 4-Digit OTP</Label>
                        <Input id="otp" value={otp} onChange={(e) => setOtp(e.target.value)} maxLength={4} placeholder="1234" />
                    </div>
                    <Button onClick={handleVerifyOtp} disabled={isProcessing || otp.length !== 4} className="w-full">
                        {isProcessing ? <Loader2 className="mr-2 h-4 w-4 animate-spin"/> : <UserCheck className="mr-2 h-4 w-4" />}
                        Verify OTP
                    </Button>
                </div>
            )}
          </div>
        );
      case "face":
        return (
           <div className="space-y-4 text-center">
                <div className={cn(
                    "w-48 h-48 mx-auto rounded-full flex items-center justify-center bg-muted overflow-hidden relative",
                    isProcessing && "animate-pulse"
                )}>
                    <FaceIcon className="h-24 w-24 text-muted-foreground"/>
                    {isProcessing && <div className="absolute inset-0 bg-green-500/20" />}
                </div>
                <p className="text-sm text-muted-foreground">Simulating face and liveness check...</p>
                <Button onClick={handleFaceScan} disabled={isProcessing} className="w-full">
                     {isProcessing ? <Loader2 className="mr-2 h-4 w-4 animate-spin"/> : <FaceIcon className="mr-2 h-4 w-4" />}
                    {isProcessing ? 'Scanning...' : 'Start Face Scan'}
                </Button>
           </div>
        );
      case "fingerprint":
         return (
           <div className="space-y-4 text-center">
                <div className="w-48 h-48 mx-auto rounded-full flex items-center justify-center bg-muted">
                    <Fingerprint className={cn("h-24 w-24 text-muted-foreground", isProcessing && "text-primary animate-pulse")} />
                </div>
                <p className="text-sm text-muted-foreground">Simulating device biometric authentication.</p>
                <Button onClick={handleFingerprintScan} disabled={isProcessing} className="w-full">
                     {isProcessing ? <Loader2 className="mr-2 h-4 w-4 animate-spin"/> : <Fingerprint className="mr-2 h-4 w-4" />}
                    {isProcessing ? 'Scanning...' : 'Scan Fingerprint'}
                </Button>
           </div>
        );
      case "complete":
        return (
            <div className="text-center space-y-4">
                <CheckCircle className="h-16 w-16 text-green-500 mx-auto"/>
                <p>You have successfully passed all security checks. You can now proceed with the payment or other secure action.</p>
                <Alert>
                    <AlertTriangle className="h-4 w-4" />
                    <AlertTitle>Next Steps</AlertTitle>
                    <AlertDescription>
                        This verified state would be stored securely as a short-lived token to authorize the next action (e.g., `createPaymentIntent`).
                    </AlertDescription>
                </Alert>
                <Button onClick={handleReset} className="w-full">Start Over</Button>
            </div>
        )
    }
  };

  const CurrentIcon = stepConfig[step].icon;

  return (
    <Card>
      <CardHeader>
        <div className="flex items-center gap-4">
          <div className="p-3 bg-primary/10 rounded-full">
             <CurrentIcon className="h-6 w-6 text-primary" />
          </div>
          <div>
            <CardTitle>{stepConfig[step].title}</CardTitle>
            <CardDescription>{stepConfig[step].description}</CardDescription>
          </div>
        </div>
      </CardHeader>
      <CardContent>
        {renderStepContent()}
      </CardContent>
    </Card>
  );
}
